.site-header {
  display: flex;
  align-items: center; /* Vertically center content */
  width: 100%;
  padding: 15px 30px;
  background-color: #f2f2f2;
  border-bottom: 2px solid #ddd;
  box-sizing: border-box;
}

.college-logo {
  width: 80px; /* Fixed width for the logo */
  height: auto;
  margin-right: 20px; /* Space between logo and text */
  flex-shrink: 0; /* Prevent logo from shrinking */
}

.college-info {
  display: flex;
  flex-grow: 1; /* Allow the text to grow and take up remaining space */
  justify-content: center; /* Center text horizontally */
}

.college-info h1 {
  font-size: 48px; /* You can adjust this size */
  margin: 0;
  color: #ff6600;
  line-height: 1.2;
  text-align: center; /* Center the text */
  width: 100%; /* Make sure the heading stretches across the available space */
}

.website-link {
  margin-top: 5px;
  align-self: flex-end; /* Move link to the right */
}

.website-link a {
  font-size: 18px;
  color: #0073e6;
  text-decoration: none;
}

.website-link a:hover {
  text-decoration: underline;
}
.site-header {
  background-image: url('RAma.jpeg'); /* Correct URL */
  background-size: cover;                  /* Cover the entire header */
  background-position: center;             /* Center the image */
  position: relative;                      /* Needed if adding overlay */
}
.site-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent overlay */
  z-index: 0;
}

.site-header * {
  position: relative;
  z-index: 1; /* Ensure text/logo appears above overlay */
}

body {
  background-image: url('RAMA1.png');
  background-size: 100% auto;      /* Width 80%, height auto */
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}
body::before {
  content: "";
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}


