.App {
  font-family: Arial, sans-serif;
}

.chat-container {
  width: 400px;
  margin: 50px auto;
  background: #f8f9fa;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  padding: 15px;
}

.chat-box {
  height: 400px;
  overflow-y: auto;
  padding: 10px;
  background: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.message {
  margin: 8px 0;
  padding: 10px;
  border-radius: 10px;
  width: fit-content;
  max-width: 70%;
}

.user {
  background: #007bff;
  color: white;
  align-self: flex-end;
  margin-left: auto;
}

.bot {
  background: #e9ecef;
  color: black;
  align-self: flex-start;
}

.input-box {
  display: flex;
  margin-top: 10px;
}

.input-box input {
  flex: 1;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid gray;
}

.input-box button {
  margin-left: 10px;
  padding: 10px 15px;
  border: none;
  border-radius: 10px;
  background: #007bff;
  color: white;
  cursor: pointer;
}

.input-box button:hover {
  background: #0056b3;
}

/* Header.css */
.site-header {
  display: flex;
  align-items: center;
  padding: 15px 30px;
  background-color: #f2f2f2;
  border-bottom: 2px solid #ddd;
}

.college-logo {
  width: 80px;
  height: auto;
  margin-right: 20px;
}

.college-info h1 {
  font-size: 24px;
  margin: 0;
}

.college-info a {
  color: #0073e6;
  text-decoration: none;
}

.college-info a:hover {
  text-decoration: underline;
}
